*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}.buttons{margin-top:50px;text-align:center;border-radius:100px}.blob-btn{z-index:1;position:relative;padding:5px 10px;margin-bottom:30px;text-align:center;text-transform:uppercase;color:#9acd32;font-size:10px;font-weight:bold;background-color:transparent;outline:none;border:none;transition:color .5s;cursor:pointer;border-radius:100px}.blob-btn:before{content:"";z-index:1;position:absolute;left:0;top:0;width:100%;height:100%;border:1px solid #9acd32;border-radius:100px}.blob-btn:after{content:"";z-index:-2;position:absolute;left:1.5px;top:1.5px;width:100%;height:100%;transition:all .3s .2s;border-radius:100px}.blob-btn:hover{color:#fff;border-radius:100px}.blob-btn:hover:after{transition:all .3s;left:0;top:0;border-radius:100px}.blob-btn__inner{z-index:-1;overflow:hidden;position:absolute;left:0;top:0;width:100%;height:100%;border-radius:100px;background:transparent}.blob-btn__blobs{position:relative;display:block;height:100%;-webkit-filter:url("#goo");filter:url("#goo")}.blob-btn__blob{position:absolute;top:1px;width:30%;height:100%;background:#9acd32;border-radius:100%;-webkit-transform:translate3d(0, 150%, 0) scale(1.7);transform:translate3d(0, 150%, 0) scale(1.7);transition:-webkit-transform .45s;transition:transform .45s;transition:transform .45s, -webkit-transform .45s}@supports((-webkit-filter: url("#goo")) or (filter: url("#goo"))){.blob-btn__blob{-webkit-transform:translate3d(0, 150%, 0) scale(1.4);transform:translate3d(0, 150%, 0) scale(1.4)}}.blob-btn__blob:nth-child(1){left:0%;transition-delay:0s}.blob-btn__blob:nth-child(2){left:30%;transition-delay:.08s}.blob-btn__blob:nth-child(3){left:60%;transition-delay:.16s}.blob-btn__blob:nth-child(4){left:90%;transition-delay:.24s}.blob-btn:hover .blob-btn__blob{-webkit-transform:translateZ(0) scale(1.7);transform:translateZ(0) scale(1.7)}@supports((-webkit-filter: url("#goo")) or (filter: url("#goo"))){.blob-btn:hover .blob-btn__blob{-webkit-transform:translateZ(0) scale(1.4);transform:translateZ(0) scale(1.4)}}
.banner-slide{
    /* filter: grayscale(1); */
    -webkit-animation: 3s background-slide;
            animation: 3s background-slide;
} 

@-webkit-keyframes background-slide{
    from{
        left: -300px;
        opacity: 0;
    }
    to{
        left:  0px;
        opacity: 1;
    }
} 

@keyframes background-slide{
    from{
        left: -300px;
        opacity: 0;
    }
    to{
        left:  0px;
        opacity: 1;
    }
}
